import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";
import Header from "./HeaderComp";

export default function Home() {
	const [text, setText] = useState('Developer');

	useEffect(() => {
		const mediaQuery = window.matchMedia('(max-width: 900px)');
		const handleMediaQueryChange = () => {
			if (mediaQuery.matches) {
				setText('Dev');
			} else {
				setText('Developer');
			}
		};

		mediaQuery.addListener(handleMediaQueryChange);
		return () => mediaQuery.removeListener(handleMediaQueryChange);
	});

    return (
		<motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
			<Header />
			<main>
				<h2><motion.span initial={{ opacity: 0, transition: "3s" }} animate={{ opacity: 1, transition: "3s" }} exit={{ opacity: 0, transition: "3s" }}>Web</motion.span><br/><motion.span initial={{ opacity: 0, transition: "3s" }} animate={{ opacity: 1, transition: "3s" }} exit={{ opacity: 0, transition: "3s" }}>{text}</motion.span></h2>
				<picture>
					<img src="/img/IMG_2067.jpg" alt="Me" draggable="false" />
				</picture>
			</main>
			<footer>
				<p><i className="fa-solid fa-location-crosshairs"></i> Strasbourg, FR</p>
				<ul>
					<li><a href="https://www.instagram.com/gumball0670.css/"><i className="fa-brands fa-instagram"></i></a></li>
					<li><a href="https://github.com/LelouchFR"><i className="fa-brands fa-github"></i></a></li>
					<li><a href="https://www.linkedin.com/in/baptiste-zahnow/"><i className="fa-brands fa-linkedin"></i></a></li>
					<li><a href="https://twitter.com/_Mes_Reves_"><i className="fa-brands fa-twitter"></i></a></li>
				</ul>
			</footer>
		</motion.div>
    );
}